.wp-block-rwstripe-restricted-content {
	background: rgba(237, 239, 240, 0.8);
	border-bottom: 5px solid #8d96a0;
	border-top: 5px solid #8d96a0;
}
.wp-block-rwstripe-restricted-content .rwstripe-block-title {
    color: #555d66;
    display: block;
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
    font-weight: 600;
    padding: 16px;
    position: relative;
    text-transform: uppercase;
}
.wp-block-rwstripe-restricted-content .components-panel__body,
.wp-block-rwstripe-restricted-content .components-panel__body.is-opened {
	background: rgba(237, 239, 240, 0.8);
	border-bottom: none;
	border-top: none;
}
.wp-block-rwstripe-restricted-content .rwstripe-block-inspector-scrollable {
	border: 1px solid #CCC;
	margin: 0px 16px 16px 16px;
}
.wp-block-rwstripe-restricted-content .block-editor-inner-blocks {
	background-color: #FFF;
	padding: 16px;
}